<template>
  <div class="tableIndex">
    <a-table
      :dataSource="data"
      :columns="columns"
      style="width: 100%;"
      :pagination="pagination"
      @change="paginationChange"
    >
      <template #bodyCell="{ column, record }">
        <template v-if="column.key === 'type'">
          <div v-if="record.type===0">正常邮件</div>
          <div v-if="record.type===1">恶意邮件</div>
        </template>
        <template v-if="column.key === 'status'">
          <div
            v-if="record.status===0"
            style="color:rgba(55,179,101,1)"
          >放行</div>
          <div
            v-if="record.status===1"
            style="color:rgba(242,65,95,1)"
          >拦截</div>
          <div
            v-if="record.status===2"
            style="color:#f6a800"
          >检测中</div>
          <div
            v-if="record.status===3"
            style="color:#5ec3ff"
          >模型解析失败</div>
        </template>
        <template v-if="column.key === 'action'">
          <a-button
            v-for="(item,index) in actions"
            :key="index"
            type="primary"
            @click="handleAction(item.name, record.id)"
          >
            {{ item.label }}
          </a-button>
        </template>
      </template>
    </a-table>
  </div>
</template>

<script>
export default {
  name: 'tableIndex',
  props: {
    columns: {
      type: Object,
      required: true
    },
    dataSource: {
      type: Object,
      required: true
    },
    actions: {
      type: Object,
      required: false
    },
    pagination: {
      type: Object,
      required: false
    }
  },
  data () {
    return {
      data: this.dataSource
    }
  },
  methods: {
    handleAction (actionName, id) {
      this.$emit(`handle-${actionName}`, id);
    },
    paginationChange (page) {
      this.$emit('change', page);
    }
  },
  watch: {
    dataSource (newVal) {
      this.data = newVal
    },
    data (newVal) {
      this.$emit('update:dataSource', newVal);
    }
  }
}
</script>

<style lang='scss' scoped>
.tableIndex {
  display: flex;
  overflow: auto;
  height: 100%;
  :deep(.ant-table-wrapper) {
    .ant-table {
      background: transparent;
    }
    .ant-table-cell {
      padding: 0.8vw;
      background: rgba(94, 164, 255, 0.1);
      color: white;
      font-size: calc(0.3em + 0.6vw);
      letter-spacing: 0.05vw;
      button {
        padding: 0.2vw 0.75vw;
        font-size: calc(0.3em + 0.6vw);
        height: fit-content;
      }
    }
    .ant-table-cell::before {
      background: transparent !important;
    }
    td {
      background: rgba(94, 164, 255, 0.1) !important;
    }
    .ant-table-pagination {
      width: fit-content;
      float: right;
      background: rgba(94, 164, 255, 0.1);
      border-radius: 0.3vw;
      padding: 0.3vw 0.2vw;
      .ant-pagination-prev {
        svg {
          color: white;
        }
      }
      .ant-pagination-next {
        svg {
          color: white;
        }
      }
      .ant-pagination-item-active {
        border: none;
        border-color: rgb(22, 130, 252);
        background: rgb(22, 130, 252);
      }
      .ant-pagination-item {
        border: none;
        a {
          color: white;
        }
        a:hover {
          background: rgba(22, 130, 252, 0.3);
          border-radius: 0.25vw;
          transition: background-color 0.2s;
        }
      }
    }
    .ant-empty {
      .ant-empty-image {
        svg {
        }
      }
      .ant-empty-description {
        color: white;
      }
    }
  }
}
</style>