<template>
  <div class="mailIndex">
    <a-tabs
      v-model:activeKey="activeKey"
      @change="changeTabs"
    >
      <a-tab-pane
        key="mail"
        tab="邮件列表"
      >
        <div style="display: flex;flex-direction:column;height:100%;gap:1vw">
          <div class="query-group">
            <InputSearch
              placeholder="请输入邮件标题"
              :inputValue="mailQueryForm.title"
              @search="getMailList"
            ></InputSearch>
            <InputSearch
              placeholder="请输入发件人"
              :inputValue="mailQueryForm.senders"
              @search="getMailList"
            ></InputSearch>
            <InputSearch
              placeholder="请输入收件人"
              :inputValue="mailQueryForm.recipients"
              @search="getMailList"
            ></InputSearch>
            <Select
              :options="mailTypeOptions"
              :selectValue="mailQueryForm.type"
              placeholder="全部类型"
              @change="getMailList"
            ></Select>
            <Select
              :options="mailStatusOptions"
              :selectValue="mailQueryForm.status"
              placeholder="全部状态"
              @change="getMailList"
            ></Select>
            <div class="upload-icon flex-center">
              <CloudUploadOutlined @click="openUpload" />
            </div>
          </div>
          <Table
            :dataSource="mailDataSource"
            :columns="mailcolumns"
            :actions="[{
              name: 'check',
              label:'查看'
            }]"
            @handle-check="handleCheckMail"
            :pagination="pagination_mail"
            @change="mailTableChange"
          >
          </Table>
        </div>
      </a-tab-pane>
      <a-tab-pane
        key="user"
        tab="用户列表"
        force-render
      >
        <div style="display: flex;flex-direction:column;height:100%;gap:1vw">
          <div
            class="query-group"
            style="width:37%"
          >
            <InputSearch
              placeholder="请输入用户名"
              :inputValue="userQueryForm.name"
              @search="getUserList"
            ></InputSearch>
            <Select
              :options="userTypeOptions"
              :selectValue="userQueryForm.type"
              placeholder="全部类型"
              @change="getUserList"
            ></Select>
          </div>
          <Table
            :dataSource="userDataSource"
            :columns="usercolumns"
            :actions="[{
              name: 'check',
              label:'查看'
            }]"
            @handle-check="handleCheckUser"
            :pagination="pagination_user"
            @change="userTableChange"
          >
          </Table>
        </div>
      </a-tab-pane>
    </a-tabs>
    <a-modal
      v-model:open="uploadOpen"
      @ok="handleCreateOk"
      :footer="null"
    >
      <Card
        title="上传测试邮件"
        style="position: relative;"
      >
        <img
          src="@/assets/svg/upload_background.svg"
          style="position:absolute;width:100%;height:100%;top:0vw;-webkit-user-drag: none;z-index: -1;"
        >

        <div
          class="flex-coloum"
          style="width:100%;height:100%;padding:1vw 0"
        >
          <a-spin
            :spinning="spinning"
            tip="邮件处理中，请稍后..."
            style="width: 100%;"
          >
            <a-upload-dragger
              v-model:fileList="fileList"
              name="file"
              action="/api/upload/uploadFile"
              @change="handleUpload"
              :before-upload="beforeUpload"
              class="docUpload"
              :multiple="true"
            >
              <div
                class="upload-dragPart"
                style="display:flex"
              >
                <div
                  style="flex:2"
                  class="upload-svg"
                >
                  <CloudUploadOutlined />
                </div>
                <div
                  style="flex:5"
                  class="upload-text"
                >
                  <div class="upload-title">拖拽文件，或者点击 <span class="upload-strong">选择文件</span> 上传</div>
                  <div class="upload-subtitle">支持格式：pdf、doc、docx、msg</div>
                  <div class="upload-subtitle">单个文件大小限制10G以内</div>
                </div>
              </div>
              <template #itemRender="{ file }">
                <div class="renderFile-template">
                  <div class="flex-1 flex-center">
                    <img
                      class="img"
                      :src="require('@/assets/139mail.jpg')"
                      v-if="file.name.endsWith('.eml')"
                    >
                    <div
                      class="unknow-box"
                      v-else
                    >
                      <FileUnknownOutlined />
                    </div>
                  </div>
                  <div class="template-name">{{ file.name }}</div>
                  <div
                    v-if="file.status==='uploading'"
                    class="loading-icon"
                  >
                    <LoadingOutlined />
                  </div>
                  <div
                    class="success-status"
                    v-else-if="file.status==='done'"
                  >上传成功</div>
                  <div
                    class="fail-status"
                    v-else
                  >上传失败</div>
                  <div class="delete-icon">
                    <DeleteOutlined @click="deleteFile(file)" />
                  </div>
                </div>
              </template>
            </a-upload-dragger>
            <div
              style="display:flex;flex-direction:row-reverse;margin-top:10px;color:white;padding:0 1vw"
              v-if="fileList.length>0"
            >上传文档数：{{ createDataByListForm.length }}/{{ fileList.length }}</div>
            <div class="modal-btn">
              <a-button
                type="primary"
                danger
                style="position: relative;right:1vw"
                @click="handleCancel"
              >取消</a-button>
              <a-button
                type="primary"
                style="position: relative;right:1vw"
                :disabled="createDataByListForm.length===0"
                @click="handleUploadOk"
              >完成</a-button>
            </div>
          </a-spin>
        </div>
      </Card>
    </a-modal>
  </div>
</template>

<script>
import InputSearch from '@/components/inputSearch.vue'
import Select from '@/components/select.vue'
import Table from '@/components/table.vue'
import Card from '@/components/card.vue'
import { CloudUploadOutlined, DeleteOutlined, LoadingOutlined, FileUnknownOutlined } from '@ant-design/icons-vue'
// eslint-disable-next-line
import { getMailList, getUserList, addEmailByList } from '@/api/mail'
import { message } from 'ant-design-vue'
export default {
  name: 'mailIndex',
  components: {
    CloudUploadOutlined, DeleteOutlined, LoadingOutlined, FileUnknownOutlined,
    InputSearch, Select, Table, Card
  },
  data () {
    return {
      spinning: false,
      activeKey: 'mail',
      uploadOpen: false,
      fileList: [],
      createDataByListForm: [],
      mailQueryForm: {
        pageNum: 1,
        title: '',
        senders: '',
        recipients: '',
        type: undefined,
        status: undefined
      },
      mailDataSource: [],
      mailcolumns: [{
        title: 'ID',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: '邮件标题',
        dataIndex: 'title',
        key: 'title',
      }, {
        title: '发件人',
        dataIndex: 'senders',
        key: 'senders',
      },
      {
        title: '收件人',
        dataIndex: 'recipients',
        key: 'recipients',
      },
      {
        title: '类型',
        dataIndex: 'type',
        key: 'type',
      },
      {
        title: '状态',
        dataIndex: 'status',
        key: 'status',
      },
      {
        title: '发送日期',
        dataIndex: 'sendDate',
        key: 'sendDate',
      },
      {
        title: '操作',
        key: 'action',
      }],
      mailTypeOptions: [{
        value: 0,
        label: '正常邮件',
      },
      {
        value: 1,
        label: '恶意邮件',
      }],
      mailStatusOptions: [{
        value: 0,
        label: '放行',
      },
      {
        value: 1,
        label: '拦截',
      }, {
        value: 2,
        label: '检测中',
      },
      {
        value: 3,
        label: '模型解析失败',
      }],
      userQueryForm: {
        name: '',
        type: undefined
      },
      userDataSource: [
        {
          "id": "1",
          "mailBox": "xxx@139.com",
          "type": "通知",
          "sendDate": "2024-09-10 08:30:00"
        },
        {
          "id": "2",
          "mailBox": "xxx@139.com",
          "type": "工作",
          "sendDate": "2024-09-09 17:45:00"
        },
        {
          "id": "3",
          "mailBox": "xxx@139.com",
          "type": "营销",
          "sendDate": "2024-09-08 10:15:00"
        },
        {
          "id": "4",
          "mailBox": "xxx@139.com",
          "type": "通知",
          "sendDate": "2024-09-07 09:00:00"
        },
        {
          "id": "5",
          "mailBox": "xxx@139.com",
          "type": "通知",
          "sendDate": "2024-09-06 14:20:00"
        }
      ],
      usercolumns: [{
        title: 'ID',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: '用户邮箱',
        dataIndex: 'mailBox',
        key: 'mailBox',
      },
      {
        title: '类型',
        dataIndex: 'type',
        key: 'type',
      },
      {
        title: '最近活跃',
        dataIndex: 'sendDate',
        key: 'sendDate',
      },
      {
        title: '操作',
        key: 'action',
      }],
      userTypeOptions: [{
        value: 'jack',
        label: 'Jack',
      },
      {
        value: 'lucy',
        label: 'Lucy',
      }],
      pagination_mail: {
        current: 1,
        total: 5,
        defaultPageSize: 10,
        showSizeChanger: false
      },
      pagination_user: {
        current: 1,
        total: 5,
        defaultPageSize: 10,
        showSizeChanger: false
      },
    }
  },
  mounted () {
    this.getMailList()
  },
  methods: {
    getMailList () {
      this.mailQueryForm.pageNum = this.pagination_mail.current
      getMailList(this.mailQueryForm).then((data) => {
        this.mailDataSource = data.contentData
        this.pagination_mail.total = data.totalSize
      })
    },
    getUserList () {
      this.userQueryForm.pageNum = this.pagination_user.current
      // getUserList(this.userQueryForm).then((data) => {
      //   this.userDataSource = data.records
      //   this.pagination_user.total = data.total
      // })
    },
    mailTableChange (page) {
      this.pagination_mail.current = page.current
      this.getMailList()
    },
    userTableChange (page) {
      this.pagination_user.current = page.current
      this.getUserList()
    },
    changeTabs (value) {
      this.activeKey = value
      if (this.activeKey === 'mail') {
        this.getMailList()
      } else if (this.activeKey === 'user') {
        this.getUserList()
      }
    },
    handleCheckMail (id) {
      this.$router.push('/mail/mailDetail/' + id)
    },
    handleCheckUser (id) {
      this.$router.push('/mail/userDetail/' + id)
    },
    openUpload () {
      this.uploadOpen = true
    },
    handleCancel () {
      this.uploadOpen = false
      this.createDataByListForm = []
      this.fileList = []
    },
    handleUpload (info) {
      if (info.file.status === 'uploading') {
        // console.log(info.file, info.fileList)
      }
      if (info.file.status === 'done') {
        this.createDataByListForm.push({
          filePath: info.file.response.result.path,
          fileName: info.file.response.result.originalName
        })
      } else if (info.file.status === 'error') {
        console.log(info.file.name + '上传失败')
      }
    },
    beforeUpload (file) {
      const validTypes = ['message/rfc822']
      const maxFileSize = 10 * 1024 * 1024 * 1024
      if (!validTypes.includes(file.type) && !file.name.endsWith('.eml')) {
        message.warning('只能上传 eml 文件!')
        return false
      } else if (file.size > maxFileSize) {
        message.warning('文件大小不能超过10GB!')
        return false
      }
      return true
    },
    handleUploadOk () {
      this.spinning = true
      addEmailByList(this.createDataByListForm).then((data) => {
        if (data.errorMsg) {
          message.warning(data.errorMsg)
        }
        this.createDataByListForm = []
        this.fileList = []
      }).catch((error) => {
        console.log(error)
      }).finally(() => {
        this.uploadOpen = false
        this.spinning = false
        this.getMailList()
      })
    },
    deleteFile (file) {
      this.fileList = this.fileList.filter(f => f.uid !== file.uid)
      this.createDataByListForm = this.createDataByListForm.filter(item => item.name !== file.name)
    },
  }
}
</script>

<style lang="scss" scoped>
.mailIndex {
  display: flex;
  width: 100%;
  height: 100%;
  padding: 0 2vw;
  .query-group {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2vw;
    .upload-icon {
      cursor: pointer;
      :deep(svg) {
        color: rgb(128, 194, 255);
        width: 2vw;
        height: 2vw;
      }
    }
  }
  :deep(.ant-tabs) {
    width: 100%;
    height: 100%;
    .ant-tabs-nav {
      margin-bottom: 1vw;
    }
    .ant-tabs-nav-list {
      border-bottom: 0.13vw rgba(255, 255, 255, 0.7) solid;
      width: 100%;
      .ant-tabs-tab {
        padding: 0.7vw 0;
        [aria-selected="false"] {
          color: white;
        }
        .ant-tabs-tab-btn {
          font-size: calc(0.3em + 0.7vw);
          font-weight: bold;
          letter-spacing: 0.1vw;
        }
      }
      .ant-tabs-ink-bar {
        height: 0.13vw;
        bottom: -0.13vw;
      }
    }
    .ant-tabs-content-holder {
      height: 100%;
    }
    .ant-tabs-content {
      height: 100%;
    }
  }
}
.modal-btn {
  display: flex;
  flex-direction: row-reverse;
  width: 100%;
  margin-top: 1vw;
  gap: 1vw;
  button[disabled] {
    color: white;
    background: #1677ff;
    border: none;
  }
}
.docUpload {
  .upload-dragPart {
    .upload-svg {
      display: flex;
      align-items: center;
      justify-content: center;
      :deep(svg) {
        width: 6em;
        height: 6em;
        color: rgb(209, 229, 253);
      }
    }
    .upload-text {
      display: flex;
      flex-direction: column;
      justify-content: center;
      .upload-title {
        color: white;
        font-size: calc(0.35em + 0.72vw);
        font-family: SourceHanSansSC;
        .upload-strong {
          color: #7ac3ff;
        }
      }
      .upload-subtitle {
        color: rgb(213, 213, 213);
        font-size: calc(0.2em + 0.7vw);
      }
    }
  }
  .renderFile-template {
    z-index: 1;
    display: flex;
    // border: 2px solid rgb(209, 229, 253);
    border-radius: 8px;
    margin-top: 10px;
    padding: 0.2vw;
    align-items: center;
    overflow-y: auto;
    overflow-x: hidden;
    max-height: 500px;
    color: white;
    .unknow-box {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      justify-content: space-around;
      :deep(svg) {
        width: 1.5em;
        height: 1.5em;
        color: white;
      }
    }
    .img {
      width: 1.3vw;
      height: 1.3vw;
    }
    .template-name {
      flex: 3;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      -webkit-line-clamp: 1;
    }
    .success-status {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      color: rgba(8, 148, 89, 1);
      font-weight: bold;
    }
    .fail-status {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      color: rgba(245, 13, 52, 1);
    }
    .loading-icon {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      :deep(svg) {
        color: rgb(252, 167, 22);
      }
    }
    .delete-icon {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      :deep(svg) {
        color: rgba(245, 13, 52, 1);
      }
    }
  }
}
:deep(.ant-upload-list) {
  max-height: 15vw;
  overflow: auto;
}
</style>