<template>
  <a-select
    v-model:value="value"
    style="width: 100%"
    @change="handleChange"
    :options="options"
    :allowClear="true"
    :placeholder="placeholder"
  ></a-select>
</template>

<script>
export default {
  name: 'selectIndex',
  props: {
    selectValue: {
      type: String,
      required: true
    },
    options: {
      type: Object,
      required: true
    },
    placeholder: {
      type: String,
      required: false
    }
  },
  data () {
    return {
      value: this.selectValue
    }
  },
  watch: {
    selectValue (newVal) {
      this.value = newVal
    },
    value (newVal) {
      this.$emit('update:selectValue', newVal);
    }
  }
}
</script>

<style lang="scss" scoped>
:deep(.ant-select-selection-placeholder) {
  color: rgba(255, 255, 255, 0.7);
  display: flex;
  align-items: center;
  font-size: calc(0.3em + 0.6vw);
}
:deep(.ant-select-selector) {
  background: rgba(94, 164, 255, 0.2) !important;
  border: 0.05vw solid transparent !important;
  height: 2vw !important;
  .ant-select-selection-item {
    color: white;
    display: flex;
    align-items: center;
    font-size: calc(0.3em + 0.6vw);
  }
}
:deep(.ant-select-arrow) {
  color: rgba(255, 255, 255, 0.4);
}
:deep(.ant-select-clear) {
  background: transparent;
  color: rgba(255, 255, 255, 0.7);
}
:deep(.ant-select-clear:hover) {
  background: transparent;
  color: rgba(255, 255, 255, 0.7);
}
</style>