<template>
  <a-input-search
    v-model:value="value"
    :placeholder="placeholder"
    enter-button
    style="width: 100%;"
  />
</template>

<script>
export default {
  name: 'inputSearch',
  props: {
    inputValue: {
      type: String,
      required: true
    },
    placeholder: {
      type: String,
      required: false
    }
  },
  data () {
    return {
      value: this.inputValue
    }
  },
  watch: {
    inputValue (newVal) {
      this.value = newVal
    },
    value (newVal) {
      this.$emit('update:inputValue', newVal);
    }
  }
}
</script>

<style lang="scss" scoped>
:deep(.ant-input) {
  background: rgba(94, 164, 255, 0.2);
  color: white;
  font-size: calc(0.3em + 0.6vw);
  border: 0.05vw solid transparent;
  // top: 0.05vw;
  left: 0.05vw;
  height: 2vw;
}
:deep(input::-webkit-input-placeholder) {
  color: rgba(255, 255, 255, 0.7);
  font-size: calc(0.3em + 0.6vw);
}

:deep(.ant-input-group-addon) {
  button {
    background: rgba(94, 164, 255, 0.25) !important;
    height: 2vw;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
</style>